@import url(http://fonts.cdnfonts.com/css/doge-sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.chat {
    flex: 1 1;
    font-size: large;
    overflow-y: scroll;
}

.window {
    flex: 1 1;
    margin: 0.5em 0.5em 0 0.5em;
    min-height: 0;
    display: flex;
}
.send-message {
    grid-area: s;
    display: flex;
    padding: 15px 300px;
    position: fixed;
    bottom: 0;
}
.send-message input {
    width: 100%;
    padding: 15px 300px;
    margin: 0;
    font-weight: 200;
    text-align: left bottom;
    display: flex;
}

.userListContainer {
    grid-area: u;
    float:right; 
    width:200px;
    border-style: solid;
    text-align: center;
    align-items: center;
}

.userList {
    list-style-type:none;
    text-align: left;
}
.app {
  display: flex;
  height: 100%;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: system-ui;
  font-weight: 600;
  color: 	#5e90b1; 
}

.header {
  padding: 20px;
  text-align: center;
  background: #1abc9c;
  color: white;
  font-size: 40px;
  font-family: 'Doge Sans', sans-serif;
}

.send-message {
  grid-area: s;
}

.messageListContainer {
  grid-area: m;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loginContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}


.messageListContainer {
    list-style-type:none;
    height: 500px;
    font-size: 18px;
    font-weight: 300;

    overflow: scroll;
}
