@import url('http://fonts.cdnfonts.com/css/doge-sans');

.app {
  display: flex;
  height: 100%;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: system-ui;
  font-weight: 600;
  color: 	#5e90b1; 
}

.header {
  padding: 20px;
  text-align: center;
  background: #1abc9c;
  color: white;
  font-size: 40px;
  font-family: 'Doge Sans', sans-serif;
}

@import './components/Chat/style.css';
@import './components/SendUserMessage/style.css';
@import './components/UserList/style.css';

.send-message {
  grid-area: s;
}

.messageListContainer {
  grid-area: m;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
